.product {
  height: 100%;

  &-container {
    display: flex;
    flex-direction: column;
    gap: 40px;
    max-width: 400px;
    margin: 0 auto;

    @include on-md {
      gap: 48px;
    }

    @include on-xl {
      max-width: none;
      flex-direction: row;
      gap: 24px;
    }
  }

  &__photo {
    display: block;
    width: 100%;
    height: 100%;
    margin: 0 auto;

    object-fit: contain;

    @include hover(transform, scale($image-hover-scale));

    @include on-xl {
      width: auto;
    }
  }

  &__title {
    font-size: 24px;
    font-weight: 700;
    margin-top: 32px;
    margin-bottom: 8px;
  }

  &__description {
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 16px;
  }

  &__price {
    font-size: 24px;
    font-weight: 700;
  }
}
